.slider {
    text-align: center;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.image-slider {
    
    vertical-align: middle;
    display: inline-flex;
    white-space: nowrap;
    overflow-x: hidden;
    overflow-y: hidden;
   width: 100%;
    transition: transform 0.3s;
}
.image-container{
    
    
}

.slide {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transform: translateX(-50%);
    transition: all 0.5s ease;
}

@media screen and (min-width: 600px) {
    .slide img {
        width: 100%;
        height: 100%;
    }
}

.slide img {
    height: 100%;
}

.current {
    opacity: 1;
    transform: translateX(0);
}

.content {
    position: absolute;
    top: 23rem;
    left: 5rem;
    opacity: 0;
    width: 50%;
    color: #fff;
    padding: 3rem;
    background: rgba(0, 0, 0, 0.3);
    animation: slide-up 1s ease 0.5s;
    animation-fill-mode: forwards;
    visibility: hidden;
}

@keyframes slide-up {
    0% {
        visibility: visible;
        top: 23rem;
    }

    100% {
        visibility: visible;
        top: 17rem;
    }
}

@media screen and (max-width: 600px) {
    .content {
        width: 80%;
    }
}


.content>* {
    color: #fff;
    margin-bottom: 1rem;
}

.current .content {
    opacity: 1;
    transform: translateX(0);
    transition: all 0.5s ease;
}

.arrow {
    /* border: 2px solid white; */
    background-color: transparent;
    color: rgba(15, 23, 30, .5);
    cursor: pointer;
    /* height: 2rem;
    width: 2rem; */
    /* border-radius: 50%; */
    position: absolute;
    z-index: 999;
    font-size: 20rem;
}

.arrow:hover {
    background-color: rgba(15, 23, 30, .5);
    color: #fff;
}

.next {
    top: 5%;
    right: 0;
    border-radius: 70% 0 0 70%;
}

.prev {
    top: 5%;
    left: 0;
    border-radius:  0 70% 70% 0;
}

hr {
    height: 2px;
    background: white;
    width: 50%;
}
.container {
    height: 100vh;
    width: 100vw;
    max-height: 800px;
    max-width: 1280px;
    min-height: 600px;
    min-width: 1000px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 0 auto;
}

.border {
    height: 369px;
    width: 290px;
    background: transparent;
    border-radius: 10px;
    transition: border 1s;
    position: relative;
}

.border:hover {
    border: 1px solid #fff;
}

.card {
    height: 379px;
    width: 300px;
    background: #808080;
    border-radius: 10px;
    transition: background 0.8s;
    overflow: hidden;
    background: #000;
    box-shadow: 0 70px 63px -60px #000;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    margin: 0 10px;
}

